<template>
    <button class="el-button" @click="click1" :class="[type?'el-button--' + type:'',{'is-disabled':buttonDisabled,'is-loading':loading}]">
        <i class="el-icon-loading" v-if="loading"></i>
        <i v-if="!loading&&icon" :class="icon"></i>
        <slot></slot>
    </button>
</template>
<style>

</style>

<script>
export default {
    name: 'ElButton',
    props: {
        disabled:Boolean,
        type:{
            type:String,
            default:'default'
        },
        loading:Boolean,
        icon:{
            type:String,
            default:''
        }
    },
    computed: {
        buttonDisabled() {
            return this.disabled;
        },
    },
    methods:{
        click1(){
            this.$emit('click',function(){
                alert("1")
            })
        }
    }
}
</script>



