<template>
  <div id="app">
      <el-button loading type="success">提交</el-button>
      <el-button :loading="isLoading" @click="click" icon="el-icon-edit" type="info" ></el-button>
      <el-button  type="warning">提交</el-button>
      <el-button :disabled="disabled" type="danger">提交</el-button>
      <el-button :disabled="disabled" type="primary">提交</el-button>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import ElButton from './components/ElButton'

export default {
  name: 'App',
  data(){
    return {
      disabled: false,
      isLoading:false,
    }
  },
  components: {
    "el-button": ElButton
  },
  methods:{
   click(value){
      // value()
     this.isLoading = true;
    }
  },
  mounted:function(){
    setTimeout(()=>{
      this.disabled = true
    },2000)
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



